* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 2rem;
}

input {
  width: 100%;
  font-size: 1.2rem;
  color: rgba(0, 0, 0, 0.5);
}

input::placeholder {
  color: rgba(0, 0, 0, 0.5);
}

::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.5);
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card-grid{
  display: flex;
  flex-wrap: wrap;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  width: 100%;
}
